<template>
    <div
        id="no-select"
    >
        <div class="modal-backdrop"
            v-if="startGame"
        >
            <div class="modal">
            <header class="modal-header">
                Упс, кажется, у нас мало времени. Скорее собери все полезные бонусы от {{companyName}}<br/>
                И не попадайся на кликбейты, говоят, после перехода по ним ты уже не будешь прежним...
            </header>
            <section class="modal-body">
                <slot name="body">
                    <v-btn
                        class="btn-close"
                        block
                        @click="start"
                    >Погнали!</v-btn>
                </slot>
            </section>
            </div>
        </div>
        <v-row>
            time: {{time}}
            <canvas
                height=600
                width=900
                id="canvas"
                ref="canvas"
            ></canvas>
        </v-row>
        <div class="modal-backdrop"
            v-if="endGame"
        >
            <div class="modal">
            <header class="modal-header">
            </header>
            <section class="modal-body">
                <slot name="body">
                </slot>
            </section>
            </div>
        </div>
    </div>
    
</template>

<script>
import BadWinImage from "../assets/lvl_2/bad_win.png";
import GoodWinImage from "../assets/lvl_2/good_win.png";
import ImageCloseBtn from "../assets/lvl_2/close_btn.png";

export default {
    /* eslint-disable no-console */
    name: 'levelTwo',
    data: () => ({
        startGame: true,
        endGame: false,
        companyName: '"Ваша Компания"',
        cvn: null,
        winWidth: Number,
        winHeight: Number,
        headerHeight: 50,
        time: 30,
        curBadPos: [],
        curGoodPos: [],
        closedCounter: 0,
        missCounter: 0,
        goodsCounter: 0,
    }),
    created(){
    },
    methods:{
        start(){
            this.startGame = false;
            this.timerStart();
        },
        init(){
            let cvn = this.$refs.canvas;
            this.cvn = cvn;

            this.winWidth = cvn.width * 0.3;
            this.winHeight = cvn.height *0.3;
            
            this.drawBad();
        },
        getRandPos(){
            let maxX = this.cvn.width - this.winWidth;
            let minX = 0;
            let maxY = this.cvn.height - this.winHeight;
            let minY = this.headerHeight;

            let x = Math.floor(Math.random() * (maxX - minX)) + minX;
            let y = Math.floor(Math.random() * (maxY - minY)) + minY;

            return {x: x, y: y};
        },
        drawGood(){
            let ctx = this.cvn.getContext("2d");

            let goodWin = new Image();

            goodWin.src = GoodWinImage;

            let pos = this.getRandPos();
            this.curGoodPos.push(pos);

            let width = this.winWidth;
            let height = this.winHeight;

            goodWin.onload - function(){
                ctx.drawImage(goodWin, pos.x, pos.y, width, height);
            }
        },
        drawBad(){
            let ctx = this.cvn.getContext("2d");

            let badWin = new Image();
            let closeBtn = new Image();

            badWin.src = BadWinImage;
            closeBtn.src = ImageCloseBtn;

            let pos = this.getRandPos();
            this.curBadPos.push(pos);

            let width = this.winWidth;
            let height = this.winHeight; 

            badWin.onload = function() {
                ctx.drawImage(badWin, pos.x, pos.y, width, height);
                ctx.drawImage(closeBtn, pos.x, pos.y, width*0.2, width*0.2);
            }
        },
        timerStart(){
            setInterval( () => {
				if (this.time > 0) {
                    this.time--
				}
            }, 1000 );
            setInterval( () => {
                if (Math.random() > 0.2 && this.time > 0){
                    this.drawBad();
                }
                if (Math.random() > 0.9 && this.time > 0){
                    this.drawGood();
                }
            }, 400 );
        },
        checkClick(x, y){
            let width = this.winWidth;
            let closed = 0;
            //let height = this.winHeight; 

            let sizeBadBedore = this.curBadPos.length;

            let curBadPos = this.curBadPos.filter(function(obj){
                if (!((x < obj.x || x > obj.x + width*0.2) ||
                (y < obj.y || y > obj.y + width*0.2))){
                    closed++;
                }
                return (x < obj.x || x > obj.x + width*0.2) ||
                (y < obj.y || y > obj.y + width*0.2); 
            });
            this.closedCounter += closed;
            this.curBadPos = curBadPos;

            let sizeBadAfter = this.curBadPos.length;

            let curGoodPosCopy = this.curGoodPos;    
            let sizeGoodBedore = curGoodPosCopy.length;

            let goodsCounter = 0;
            let curGoodPos = this.curGoodPos.filter(function(obj){
                if (!((x < obj.x || x > obj.x + width) ||
                (y < obj.y || y > obj.y + width))){
                    goodsCounter++;
                }
                return (x < obj.x || x > obj.x + width) ||
                (y < obj.y || y > obj.y + width); 
            });
            this.goodsCounter += goodsCounter;
            this.curGoodPos = curGoodPos;

            let sizeGoodAfter = curGoodPosCopy.length;

            for (let i = 0; i < this.curBadPos.length; ++i) {
                if ((x > this.curBadPos[i].x + width*0.2 && x < this.curBadPos[i].x + width) &&
                    (y > this.curBadPos[i].y + width*0.2 && y < this.curBadPos[i].y + width) && 
                    sizeBadBedore==sizeBadAfter && sizeGoodBedore==sizeGoodAfter) {
                    this.missCounter++;
                    break;
                }
            }


            this.redrawAll();
            this.refreshText();
        },
        redrawAll(){    
            let ctx = this.cvn.getContext("2d");
            ctx.clearRect(0, 0, this.cvn.width, this.cvn.height)

            let badWin = new Image();
            let closeBtn = new Image();
            let goodWin = new Image();

            badWin.src = BadWinImage;
            closeBtn.src = ImageCloseBtn;
            goodWin.src = GoodWinImage;

            let width = this.winWidth;
            let height = this.winHeight;

            let curBadPos = this.curBadPos;

            let curGoodPos = this.curGoodPos;

            goodWin.onload = function(){
                for (let i = 0; i < curBadPos.length; ++i){
                    let x = curBadPos[i].x;
                    let y = curBadPos[i].y;
                    ctx.drawImage(badWin, x, y, width, height);
                    ctx.drawImage(closeBtn, x, y, width*0.2, width*0.2);
                }
                for (let i = 0; i < curGoodPos.length; ++i){
                    let x = curGoodPos[i].x;
                    let y = curGoodPos[i].y;
                    ctx.drawImage(goodWin, x, y, width, height);
                }
            }
        },
        refreshText(){
            let ctx = this.cvn.getContext("2d");
            ctx.fillStyle = "black";
            ctx.font="20px Georgia";
            ctx.fillText("Одолел: " + this.closedCounter + " Промахнулся: " + this.missCounter + " Собрал ништяков:" + this.goodsCounter, 10, 50);
        },
    },
    mounted(){
        this.init();
        this.cvn.addEventListener("click", event => {
            let bound = this.cvn.getBoundingClientRect();
            let x = event.clientX - bound.left - this.cvn.clientLeft;
            let y = event.clientY - bound.top - this.cvn.clientTop;
            this.checkClick(x, y);
        }); 
    },
}
</script>

<style>
#canvas {
  border: 1px solid gray;
}

.modal-backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  background: #ffffff;
  box-shadow: 20px 20px 20px 20px;
  overflow-x: auto;
  display: flex;
  flex-direction: column;
}

.modal-header,
.modal-footer {
  padding: 15px;
  display: flex;
}

.modal-header {
  border-bottom: 1px solid #eeeeee;
  color: #000000;
  justify-content: space-between;
}

.modal-footer {
  border-top: 1px solid #eeeeee;
  justify-content: flex-end;
}

.modal-body {
  position: relative;
  padding: 10px 10px 10px 10px;
}

.btn-close {
  border: none;
  font-size: 15px;
  cursor: pointer;
  font-weight: bold;
  color: #ebac0c;
  background: transparent;
}

.btn-func {
  color: white;
  background: #ebac0c;
  margin: 10px 20px 10px 20px;
  padding: 10px;
  border: 1px solid #ebac0c;
  border-radius: 2px;
}

#no-select {
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    -khtml-user-select: none; 
}

#canvas {
    background: #ebac0c;
}

</style>